<template>
  <div>
    <canvas
      id="canvas"
      class="fl"
      width="600"
      height="400"
      @mousedown="canvasDown($event)"
      @mouseup="canvasUp($event)"
      @mousemove="canvasMove($event)"
    ></canvas>

    <!-- 画笔粗细 -->
    <span
      v-for="(pen, index) in brushs"
      :class="[pen.className,{'active': config.lineWidth === pen.lineWidth}]"
      @click="setBrush(pen.lineWidth)"
      :key="index"
    ></span>
  </div>
</template>
<script>
export default {
  data() {
    return {
      brushs: [
        {
          className: "small fa fa-paint-brush",
          lineWidth: 3
        },
        {
          className: "middle fa fa-paint-brush",
          lineWidth: 6
        },
        {
          className: "big fa fa-paint-brush",
          lineWidth: 12
        }
      ],
      ddd:{},
      context: {},
      imgUrl: [],
      canvasMoveUse: true,
      preDrawAry: [],
      nextDrawAry: [],
      middleAry: [],
      config: {
        lineWidth: 1,
        lineColor: "#f2849e",
        shadowBlur: 2
      }
    };
  },
  mounted() {
    this.show();
    // this.aaa();
  },
  methods: {
    // // 当在屏幕中移动时即开始绘制准备
    beginPath(e) {
      const canvas = document.querySelector("#canvas");
       this.ddd = canvas.getContext('2d')
      if (e.target !== canvas) {
        this.ddd.beginPath();
      }
    },
    setCanvasStyle() {
      this.context.lineWidth = this.config.lineWidth;
      this.context.shadowBlur = this.config.shadowBlur;
      this.context.shadowColor = this.config.lineColor;
      this.context.strokeStyle = this.config.lineColor;
    },
    // 在canvas中鼠标按下
    canvasDown(e) {
      // 让move方法可用
      this.canvasMoveUse = true;
      // client是基于整个页面的坐标
      // offset是cavas距离顶部以及左边的距离
      const canvasX = e.clientX - e.target.parentNode.offsetLeft;
      const canvasY = e.clientY - e.target.parentNode.offsetTop;
      // 设置canvas的配置
      this.setCanvasStyle();
      //清除子路径
      this.ddd.beginPath();
      // 移动的起点
      this.ddd.moveTo(canvasX, canvasY);
      //当前绘图表面状态
      const preData = this.context.getImageData(0, 0, 600, 400);
      //当前绘图表面进栈
      // 按下相当于新的操作的开始，所以把当前记录数据放到prev中
      this.preDrawAry.push(preData);
    },
    // canvas中鼠标移动
    canvasMove(e) {
      if (this.canvasMoveUse) {
        // 只有允许移动时调用
        const t = e.target;
        let canvasX;
        let canvasY;
        // 由于手机端和pc端获取页面坐标方式不同，所以需要做出判断
        // if(this.isPc()){
        //   canvasX = e.clientX - t.parentNode.offsetLeft
        //   canvasY = e.clientY - t.parentNode.offsetTop
        // }else {
        //   canvasX = e.changedTouches[0].clientX - t.parentNode.offsetLeft
        //   canvasY = e.changedTouches[0].clientY - t.parentNode.offsetTop
        // }
        canvasX = e.clientX - t.parentNode.offsetLeft;
        canvasY = e.clientY - t.parentNode.offsetTop;
        // 连接到移动的位置并上色
        this.ddd.lineTo(canvasX, canvasY);
        console.log(this.ddd.lineTo)
        this.ddd.stroke();
      }
    },
    // canvas中鼠标放开
    canvasUp(e) {
      const preData = this.ddd.getImageData(0, 0, 600, 400);
      if (!this.nextDrawAry.length) {
        // 在没有撤销过的情况下，将当前数据放入prev
        //当前绘图表面进栈
        this.middleAry.push(preData);
      } else {
        // 在撤销的情况下，将在后面步骤的数据情况记录
        this.middleAry = [];
        this.middleAry = this.middleAry.concat(this.preDrawAry);
        this.middleAry.push(preData);
        this.nextDrawAry = [];
      }
      // 设置move时不可绘制
      this.canvasMoveUse = false;
    },

    show() {
      // 撤销
      if (this.preDrawAry.length) {
        const popData = this.preDrawAry.pop();
        const midData = this.middleAry[this.preDrawAry.length + 1];
        this.nextDrawAry.push(midData);
        this.ddd.putImageData(popData, 0, 0);
      }
      // 前进
      if (this.nextDrawAry.length) {
        const popData = this.nextDrawAry.pop();
        const midData = this.middleAry[
          this.middleAry.length - this.nextDrawAry.length - 2
        ];
        this.preDrawAry.push(midData);
        this.ddd.putImageData(popData, 0, 0);
      }
      // 清空
      this.ddd.clearRect(
        0,
        0,
        this.context.canvas.width,
        this.context.canvas.height
      );
      // 清空前后数据
      this.preDrawAry = [];
      this.nextDrawAry = [];
      // middleAry恢复到默认数据
      this.middleAry = [this.middleAry[0]];
    },
    // aaa(){
    //       var canvas = document.getElementById("canvas");
    //     //检测浏览器是否支持canvas 该方法是否存在 取得上下文对象
    //     if (canvas.getContext) {
    //         var context = canvas.getContext("2d");
    
    //   context.fillStyle = "red";
    //   context.font = "bold 26px"; //字体样式
    //   context.textAlign = "statr"; //对齐方式为左对齐
    //   context.textBaseline = "top"; //基线
    //   context.fillText("你好", 100, 100, 200);
    
    // }
    // }
  }
};
</script>



